import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import { Empty, Collapse, Select, DatePicker, Row, Col, Divider } from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/monthlyPurchaseReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;

const redirectTo = "/dashboard/report-menus";

class MonthlyPurchaseReport extends Component {
  state = {
    filter: {
      id_location: null,
      id_supplier: null,
      month_year: moment().startOf("month")
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getMonthlyPurchaseReports } = this.props;
    getMonthlyPurchaseReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.monthlyPurchaseReport.error) {
      notification("error", this.props.monthlyPurchaseReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.monthlyPurchaseReport.success) {
      notification("success", this.props.monthlyPurchaseReport.success);
      this.props.clearSuccess();
    }
  }

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "month_year" && value) {
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          month_year: value
        }
      });
    } else if (name === "month_year" && !value) {
      this.setState({
        filter: {
          ...this.state.filter,
          month_year: null
        }
      });
    } else if (name === "id_location") {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value,
          id_supplier: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.month_year) {
      string
        ? (string +=
          "&month=" +
          filter.month_year.format("M") +
          "&year=" +
          filter.month_year.format("YYYY"))
        : (string +=
          "month=" +
          filter.month_year.format("M") +
          "&year=" +
          filter.month_year.format("YYYY"));
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getMonthlyPurchaseReports } = this.props;
    //const { current_page } = this.props.monthlyPurchaseReport;
    let filter = this.filterValue();

    getMonthlyPurchaseReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getMonthlyPurchaseReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_supplier: null,
        month_year: moment().startOf("month")
      }
    });
    getMonthlyPurchaseReports();
  };

  render() {
    // console.log(this.props);

    // Data setup
    const {
      monthly_purchase_reports,
      locations,
      suppliers,
      filter,
      filter_select
    } = this.props.monthlyPurchaseReport;
    //console.log(this.props.monthlyPurchaseReport.base_currency); //console.log(this.state);

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "day" }),
        dataIndex: "day",
        rowKey: "day",
        width: "10%"
      },
      {
        title: getLanguage({ id: "purchase.totalCash" }),
        dataIndex: "total_cash",
        rowKey: "total_cash",
        width: "45%",
        align: "right",
        parse: true,
        render: text =>
          <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "purchase.totalCredit" }),
        dataIndex: "total_credit",
        rowKey: "total_credit",
        width: "45%",
        align: "right",
        parse: true,
        render: text =>
          <NumberFormat value={parseFloat(text)} />
      }
    ];

    const dataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    monthly_purchase_reports.map((data, key) => {
      dataSource.push({
        ...data,
        key
      });
    });

    const { loading, loadingForeign } = this.props.monthlyPurchaseReport;

    const fileName = getLanguage({ id: "monthlyPurchaseReports.pageHeader" })
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="monthlyPurchaseReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="monthlyPurchaseReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
              filterDate="month_year"
              dateFormat="MMMM YYYY"
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              loading={loading}
              locations={locations}
              title="monthlyPurchaseReports.pageHeader"
            />

            <ExcelWrapper
              title="monthlyPurchaseReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
              filterDate="month_year"
              dateFormat="MMMM YYYY"
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="sidebar.supplier" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_supplier"
                      )}
                      value={
                        this.state.filter.id_supplier
                          ? this.state.filter.id_supplier
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        this.state.filter.id_location ? (
                          <IntlMessages id="select.supplier" />
                        ) : (
                            <IntlMessages id="noLocation" />
                          )
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      // loading={this.props.to_location.loadingFilterSites}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                            <Empty
                              description={<IntlMessages id="noLocation" />}
                            />
                          )
                      }
                    >
                      {suppliers.map((data, key) =>
                        data.id_location === this.state.filter.id_location ? (
                          <Option key={key} value={data.id_supplier}>
                            {`${data.supplier_code} (${data.supplier_name})`}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="monthYear" />
                        </label>
                        <br />
                        <DatePicker
                          format="MMMM YYYY"
                          allowClear={false}
                          style={{ width: "95%" }}
                          value={this.state.filter.month_year}
                          mode="month"
                          onPanelChange={value =>
                            this.handleFilterChange("month_year", value)
                          }
                        />
                      </Col>

                      <Col
                        span={12}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clickFilter" />} />
            ) : monthly_purchase_reports.length === 0 &&
              !this.props.monthlyPurchaseReport.loading ? (
                  <Empty
                    description={
                      <IntlMessages id="monthlyPurchaseReports.table.noData" />
                    }
                  />
                ) : (
                  <div className="isoInvoiceTable">
                    <Divider orientation="left" style={{ fontSize: 14 }}>
                      <IntlMessages id="details" />
                    </Divider>
                    <Scrollbars style={{ width: "100%" }}>
                      <TableWrapper
                        dataSource={dataSource}
                        columns={columns}
                        loading={loading}
                        className="invoiceListTable"
                        bordered={true}
                        // Set pagination, jika ada page_size mengunakan pagination API
                        // jika tidak ada page_size menggunakan pagination bawaan
                        pagination={false}
                      />
                    </Scrollbars>
                  </div>
                )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    monthlyPurchaseReport: state.monthlyPurchaseReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(MonthlyPurchaseReport);
